import util from '../../utils/util.js'
export default {
	data() {
		return {
			list: []
		};
	},
	onLoad() {
		this.list = [{ id: 1 }, { id: 2 }, { id: 3 }, { id: 4 }, { id: 5 }];
	},
	methods: {
		save() {
			util.success("保存成功",function(){
				uni.navigateBack({
					delta: 1
				})
			})
		}
	}
};
